@font-face {
    font-family: tesla;
    src: url('./TESLA.ttf');
}
.header{
    position: relative;
    z-index: 99;
}
.menu{
    display: none;
}

.navbar{
    color: rgb(38, 38, 38);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem .5rem 0 3rem;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
}
h1{
   
    font-size: 1.25rem;
    font-family: tesla;
    letter-spacing: .5em;
}
h1:hover{
    cursor: pointer;
}

.nav-links a{
    letter-spacing: .06em;
  
    border-radius: 0.25em;
    white-space: nowrap;
    padding: 0.35em 1em;
    font-weight: 600;
    text-decoration: none;
    color: rgb(79, 79, 79);
    font-size: 1rem;
    transition: all 0.7s;
}
.nav-links a:hover{
    background: rgba(0,0,0,0.1);
    backdrop-filter: blur(10px);
   
}


.nav-links{
    display: flex;
    justify-content: center;
}
.first{
    
    width: 55%;
    justify-content: center;
}

@media screen and (max-width: 1200px) {
    .first{
        display: none;
    }
    .account{
        display: none;
    }
    .shop{
        display: none;
    }
    .menu{
        background: rgba(0,0,0,0.07);
        backdrop-filter: blur(10px);
        border-radius: 0.25em;
        display: inline-block;
    }
    
    
}

@media screen and (max-width: 768px) {
    .navbar{
       padding-left: 1rem;
    }

  
}





